.categories{
  margin-bottom: 20px;
  display: flex;
  margin-top: 20px;
  width: 100%;
  overflow: scroll;
  overflow-y:hidden;
}

.categories::-webkit-scrollbar {
 width: 10px;
 height: 10px;
}

.categories::-webkit-scrollbar-track {
 -webkit-box-shadow: inset 0 0 6px rgba(196, 34, 34, 0.3); 
 border-radius: 10px;

}

.categories::-webkit-scrollbar-thumb {
 border-radius: 3px;
 -webkit-box-shadow: inset 0 0 6px rgb(138, 138, 177); 
 
}



@media (max-width: 760px)  {
.categories{ 
    width: 693px;
   
}   
}

@media (max-width: 530px)  {
.categories{
   
   width: 336px;}

}

