    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      background-color: gray;
      padding: 10px 10px ;
}

@media (max-width: 760px)  {
  .container{ 
      width: 693px;
     
     
}   
}

@media (max-width: 530px)  {
  .container{
width: 336px;
justify-content: center;
}
  
}

   .headerCart {
    position: relative;
    display: flex;
    align-items: center;
    padding: 9px;
    /* border-radius: 25px; */
    border-radius: 8px;
    margin-right: 5px;
  }


  .totalCount{
    padding: 3px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: blue;
    position: absolute;
    top: -4px;
    right: -4px;
}

  .headerCart:hover{
    background-color: rgb(97, 96, 108);
  }

  .countTotal{
    color: white;
    font-size: 18px;
    
  }


.wrapIcon{display: flex;}

  .icon{margin-right: 3px;
    color: white;
    font-size: 26px;
  }
  

  